import {
    Box,
    Checkbox,
    Card,
    Divider,
    List,
    ListItem,
    Typography,
    Input,
    IconButton,
} from '@mui/material'
import * as React from 'react'
import Grid from '@mui/material/Grid'
import TodoListElement from './TodoListElement'
import NewElement from './NewElement'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { checkListElement, deleteListById, updateList } from '../reducers/listReducer'

import CancelIcon from '@mui/icons-material/Cancel'
import DoneIcon from '@mui/icons-material/Done'
import DeleteIcon from '@mui/icons-material/Delete'

function TodoListsCard({ list }) {
    const dispatch = useDispatch()

    const [disabled, setDisabled] = useState(false)
    const [editing, setEditing] = useState(false)
    const [hovered, setHovered] = useState(false)

    const changeDisabled = (newState) => {
        console.log(newState);
        setDisabled(newState)
    }

    const [textValue, setTextValue] = useState(list.name)

    const handleChange = (listId, elementId) => (event) => {
        const newCheckedState = event.target.checked
        dispatch(
            checkListElement(listId.toString(), elementId, newCheckedState)
        )
    }

    const handleEdit = (listId, textValue) => {
        setEditing(false)
        setDisabled(true)
        try {
             dispatch(updateList(listId, textValue))
        } catch (exception) {
        } finally {
            setDisabled(false)
        }
    }

    const handleCancel = () => {
        setTextValue(list.name)
        setEditing(false)
    }

    const handleDelete = (listId) => {
        console.log('ejecutando accion');
        console.log(listId);
        try {
            setDisabled(true)
            dispatch(deleteListById(listId))
        } catch (e) {
            setDisabled(false)
        }
    }

    const handleTextChange = (e) => {
        setTextValue(e.target.value)
        setEditing(true)
    }

    return (
        <Grid
            item
            xs={12}
            sm={6}
            md={4}
            lg={3}
            sx={{ padding: '1rem' }}
            key={list.id}
        >
            <Card key= {list.id} sx={{ height: '100%' }} size="lg" variant="outlined">
                
                <Box
                    sx={{
                        display: 'flex',
                        justifyContent: 'flex-end',
                    }}
                >
                    <Input
                    disabled = {disabled}
                        placeholder="Placeholder"
                        onChange={(e) => handleTextChange(e)}
                        value={textValue}
                        disableUnderline={!hovered}
                        onMouseEnter={() => setHovered(true)}
                        onMouseLeave={() => setHovered(false)}
                        sx={{
                            flex: '1',
                            paddingLeft:'0.5rem',
    
                            whiteSpace: 'nowrap',
                            overflow: 'hidden',
                            textOverflow: 'ellipsis',
                            fontWeight: '600',
                            fontSize: '1.5rem',
                        }}
                    />
                    {editing ? (
                        <>
                            <IconButton
                                disabled={disabled}
                                aria-label="Done"
                                onClick={(onclick) =>
                                    handleEdit(list.id, textValue)
                                }
                            >
                                <DoneIcon />
                            </IconButton>
                            <IconButton
                                disabled={disabled}
                                aria-label="delete"
                                onClick={(onclick) => handleCancel()}
                            >
                                <CancelIcon />
                            </IconButton>
                        </>
                    ) : (
                        <IconButton
                            disabled={disabled}
                            aria-label="delete"
                            onClick={(onclick) => handleDelete(list.id)}
                        >
                            <DeleteIcon />
                        </IconButton>
                    )}
                </Box>
                <Divider inset="none" />
                <List
                    size="sm"
                    sx={{
                        mx: 'calc(-1 * var(--ListItem-paddingX))',
                    }}
                >
                    {list.listelements
                        .filter((element) => !element.checked)
                        .map((element) => {
                            return (
                                <TodoListElement
                                    key={element.id}
                                    listId={list.id}
                                    listsElement={element}
                                    disabled = {disabled} updateDisabled={changeDisabled}
                                />
                            )
                        })}
                    <ListItem>
                        <NewElement list={list.id} disabled = {disabled} updateDisabled={changeDisabled} />
                    </ListItem>

                    <Divider variant="middle" />

                    {list.listelements
                        .filter((element) => element.checked)
                        .map((element) => {
                            return (
                                <Box key={element.id}>
                                    <ListItem sx={{ height: '3rem' }}>
                                        <Checkbox
                                            checked={element.checked}
                                            onChange={handleChange(
                                                list.id,
                                                element.id
                                            )}
                                            inputProps={{
                                                'aria-label': 'controlled',
                                            }}
                                        />
                                        <Typography
                                            sx={{
                                                whiteSpace: 'nowrap',
                                                overflow: 'hidden',
                                                textOverflow: 'ellipsis',
                                            }}
                                        >
                                            {element.description}
                                        </Typography>
                                    </ListItem>
                                </Box>
                            )
                        })}
                </List>
            </Card>
        </Grid>
    )
}

export default TodoListsCard
