import {
    Box,
    Checkbox,
    IconButton,
    Input,
    ListItem,
    TextField,
    Typography,
} from '@mui/material'
import { useDispatch } from 'react-redux'
import { checkListElement, deleteListElement } from '../reducers/listReducer'
import DeleteIcon from '@mui/icons-material/Delete'
import EditIcon from '@mui/icons-material/Edit'
import CancelIcon from '@mui/icons-material/Cancel'
import DoneIcon from '@mui/icons-material/Done'
import { useState } from 'react'
import { createListElement, updateElement } from '../reducers/listReducer'

            
function TodoListsElement({ listId, listsElement, disabled, updateDisabled}) {
    const dispatch = useDispatch()

    const [editing, setEditing] = useState(false)
    const [hovered, setHovered] = useState(false)


    const [textValue, setTextValue] = useState(listsElement.description)
 

    const handleEdit = (listId, listsElement, textValue) => {
        setEditing(false)
        updateDisabled(true)
        try {
            dispatch(updateElement(listId, listsElement, textValue))
        } catch (exception) {
        }
        finally{
            
            updateDisabled(false)
        }
    }

    const handleCancel = () => {
        setTextValue(listsElement.description)
        setEditing(false)
    }

    const handleDelete = async (listId, listElementId) => {
        try {
            updateDisabled(true)
            await dispatch(deleteListElement(listId, listElementId))
        } catch (e) {
            updateDisabled(false)
        }
        finally {
            console.log('a esta parte llego');
            updateDisabled(false)
        }
    }
    const handleTextChange = (e) => {
        setTextValue(e.target.value)
        setEditing(true)
    }

    const handleChange = (listId, elementId) => (event) => {
        const newCheckedState = event.target.checked
        dispatch(
            checkListElement(listId.toString(), elementId, newCheckedState)
        )
    }

    return (
        <Box key={listsElement.id}>
            <ListItem sx={{ height: '3rem' }}>
                 
                    <>
                        <Checkbox
                            disabled={disabled}
                            checked={listsElement.checked}
                            onChange={handleChange(listId, listsElement.id)}
                            inputProps={{
                                'aria-label': 'controlled',
                            }}
                        />

                        <Input
                        disabled = {disabled}
                            placeholder="Placeholder"
                            onChange={(e) => handleTextChange(e)}
                            value={textValue}
                             disableUnderline = {!hovered} 
                             onMouseEnter={() => setHovered(true)}
                            onMouseLeave={() => setHovered(false)}
                            
                            sx={{
                                flex: '1',

                                whiteSpace: 'nowrap',
                                    overflow: 'hidden',
                                    textOverflow: 'ellipsis',

                            }}

                        />
                        <Box
                            sx={{
                                display: 'flex',
                                justifyContent: 'flex-end',
                            }}
                        >
                            {editing ? (
                                <>
                                    <IconButton
                                        disabled={disabled}
                                        aria-label="Done"
                                        onClick={(onclick) => handleEdit(listId, listsElement.id, textValue)}
                                        
                                    >
                                        <DoneIcon />
                                    </IconButton>
                                    <IconButton
                                        disabled={disabled}
                                        aria-label="delete"
                                        onClick={(onclick) => handleCancel()}
                                    >
                                        <CancelIcon />
                                    </IconButton>
                                </>
                            ) : (
                                <IconButton
                                    disabled={disabled}
                                    aria-label="delete"
                                    onClick={(onclick) =>
                                        handleDelete(listId, listsElement.id)
                                    }
                                >
                                    <DeleteIcon />
                                </IconButton>
                            )}
                        </Box>
                    </>
                
            </ListItem>
        </Box>
    )
}

export default TodoListsElement
