import {
    TextField,
} from '@mui/material'
import { useDispatch, useSelector } from 'react-redux'
import * as React from 'react'
import { createListElement } from '../reducers/listReducer'

function NewElement({ list, disabled, updateDisabled}) {
    const [textValue, setTextValue] = React.useState('')

    const dispatch = useDispatch()

    const handleSubmit = async (event, list) => {
        event.preventDefault()
        try {
            updateDisabled(true)

            const description = textValue
            await dispatch(createListElement(list, description))
            setTextValue('')
        } catch (exception) {}
        finally{
            updateDisabled(false)

        }
    }

    return (
        <form onSubmit={(e) => handleSubmit(e, list)}>
            <TextField
            disabled = {disabled}
                onChange={(e) => setTextValue(e.target.value)}
                value={textValue}
                id="standard-basic"
                label="New element"
                variant="standard"
            />
        </form>
    )
}

export default NewElement